import React, {useEffect} from 'react';

import './CallInfo.css';

const CallInfo = ({ username }) => {
  
  return (
    <div className='main_info_container'>
      <span className='main_info_text_title'>
         {username}.
      </span>
      <span className='main_info_text'>
        <p>          /   .</p>
      </span>
    </div>
  );
};

export default CallInfo;
